.App {
  /* text-align: center; */

  display: grid;
  grid-template-columns: repeat(20, 1fr);
  grid-auto-rows: repeat(20, 1fr);

  gap: 0px;

  text-align: center;

  width: 100vw;
  height: 100vh;
}

  .sidebar {
    grid-column: 1 / 2;
    grid-row: 1 / 21;

    background-color: #1E1F25;

    align-content: center;

    display: flex;
    flex-direction: column;
    max-width: 100px;


  }
  
  .sidebarBottom {
    grid-column: 1 / 2;
    grid-row: 20 / 21;

    background-color: #1E1F25;

    align-content: center;

    display: flex;
    flex-direction: column;
    max-width: 100px;
  }


  .appBodyHeader {
    grid-column: 2 / 21;
    grid-row: 1 / 1 ;
    
    background-color: #1E1F25;
    border-radius: 20px;

    margin: 10px 10px 0 10px;

    display: flex;
    justify-content: space-between;
  
  }

  .appBodyHeaderLeft {
    display: flex;
    align-items: center;
    flex-direction: row;
  }

  .appBodyContentList {
    grid-column: 2 / 17;
    grid-row: 2 / 21;

    background-color: #1E1F25;
    border-radius: 20px;

    margin: 10px 0 10px 10px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 200;
    font-size: 10px;
    line-height: 30px;
    color: #fff;

    text-align: left;
    
  }

  .appBodyContentStats {
    grid-column: 17 / 21;
    grid-row: 2 / 21;

    background-color: #1E1F25;
    border-radius: 20px;

    margin: 10px;

  }

  .sidebarImage{
    width: 40%;
    margin: 1em auto;
    }

  .sidebarLogo{
    margin: 1em auto;
    width: 90%
  }

  hr {
    background-color: #282C38;
    border: none;
    height: 1px;
    width: 100%;
  }

  #userAvatar {
    width: 80%
    
  }

  h2 {
    margin: 0;
    font-family: 'Inter';
    color: #FFF;
    font-weight: 500;

    padding-left: 1em;
   
  }


  .headerImage {
    margin-left: 1em;
  }

  .headerText {
    margin-left: 1em;

    font-family: 'Inter';
    font-size: 14px;

    color: #FFFFFF;
  }


  /* search form */

#wrap {
  display: flex;
  align-items: center;
}

#nav-search {
  margin: 0 auto;
  margin-right: 2em;
  position: relative;
  width: 202px;
}

#nav-search input{
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 13px;
  line-height: 20px;
  display: block;
  width: 163px;
  height: 2em;
  padding: 6px 27px 6px 12px;
  cursor: pointer;
  color: #757B8C;
  background-color: #282C38;
  border: none;
  border-radius: 13px 13px 13px 13px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2) inset;
}

#nav-search .icon {
  position: absolute;
  display: block;
  top: 4px;
  right: 4px;
  width: 14px;
  height: 15px;
  background: url("./images/Search.png") -20px -710px no-repeat transparent;
  border: none;
  text-indent: -200px;
  padding: 0;
  cursor: pointer;
}

